<template>
    <div>
        <raw-loader :mytxt="require('!raw-loader!./Tpl2SaveImg.vue').default"></raw-loader>
        <h1 class="border-bottom">KonvaTpl2</h1>
        {{ msg }}<br>
        {{ selId }}<br>
        <b-row>
            <b-col lg="4">
                <div class="mb-1">
                    <button class="mr-1" @click="onClearSelect">Clear Select</button>
                    <button class="mr-1" :disabled="selId == -1" @click="onFull">Full</button>
                </div>
                <div id="stage-parent">
                    <div id="container" class="border">
                        <v-stage ref="stage" @click="onStageClick" @mousemove="onStageMouseMove"
                            @mouseenter="onStageMouseenter" @mouseleave="onStageMouseleave" :config="configKonva">
                            <v-layer ref="layer" @mouseover="onmouseover" @mouseout="onmouseout">
                                <v-rect v-for="(x, k) in items" :key="k" :config="x" @dragstart="handleDragstart"
                                    @dragend="handleDragend" />
                                    
                                <v-circle ref="circle"></v-circle>
                                <v-rect ref="rect"></v-rect>
                                <v-image ref="img"></v-image>

                                <v-shape ref="custom" :config="{
                                    sceneFunc: (context, shape) => {
                                        let w = 260;
                                        let h = 170;
                                        context.beginPath();
                                        //context.rect(0, 0, w, h);
                                        context.moveTo(20, 50);
                                        context.lineTo(220, 80);
                                        context.quadraticCurveTo(150, 100, 260, 170);
                                        context.closePath();
                                        context.fillStrokeShape(shape);
                                    },
                                    fill: '#00D2FF',
                                    stroke: 'black',
                                    strokeWidth: 4,
                                    draggable: true,
                                    width: 260,
                                    height: 170
                                }" />

                                <v-text :config="{
                                    id:'thuc-1000',
                                    x: 15,//stage.width() / 2,
                                    y: 15,
                                    text: 'Thức 101',
                                    fontSize: 80,
                                    fontFamily: 'Tahoma',//'Calibri',
                                    fill: 'green',
                                    padding: 20,
                                    align: 'center',
                                    draggable:true,
                                }" />

                                <!-- <v-cursor cursor="pointer"/> -->
                                <v-transformer ref="transformer" :config="{
                                    anchorStroke: 'red',
                                    anchorFill: 'yellow',
                                    anchorSize: 8,
                                    borderStroke: 'green',
                                    borderDash: [3, 3],
                                }" />

                            </v-layer>
                        </v-stage>
                    </div>
                </div>
                <button class="mt-1" @click="onSave">Save</button>
            </b-col>
            <b-col lg="8">

                <div class="d-flex align-content-start flex-wrap">
                    <div class="p-2 bd-highlight " v-for="(x, k) in ds" :key="k">
                        <img :src="x" width="120" height="90" class="border" />
                    </div>
                </div>

            </b-col>
        </b-row>
    </div>
</template>
<script>
var sceneWidth = 1500;
var sceneHeight = 1000;
var stage = null;
var layer = null;
function generateItems() {
    let w = 200;
    let h = 200;
    let width = sceneWidth - w;
    let height = sceneHeight - h;
    const items = [];
    for (let i = 0; i < 10; i++) {
        items.push({
            x: Math.random() * width,
            y: Math.random() * height,
            radius: 50,
            id: "node-" + i,
            width: w,
            height: h,
            fill: Konva.Util.getRandomColor(),
            stroke: 'black', //Konva.Util.getRandomColor(),
            draggable: true,
            strokeWidth: 4,
        });
    }
    return items;
}
export default {
    data() {
        return {
            configKonva: {
                width: sceneWidth,
                height: sceneHeight
            },
            items: [],
            msg: '',
            count: 0,
            selId: -1,
            ds: [],
            image: null,
        }
    },
    mounted() {
        //this.items = generateItems();
        this.$nextTick(function () {
            layer = this.$refs.layer.getNode();
            stage = this.$refs.stage.getNode();
            window.addEventListener("resize", this.fitStageIntoParentContainer);
            this.fitStageIntoParentContainer();
        })
        const image = new window.Image();
        image.src = "/BoBien.jpg";
        image.onload = () => {
            this.image = image;
        };
    },
    watch: {
        selId: function (newValue, oldValue) {
            if (this.selId == -1) return;
            this.clearTransformer();
            this.addTransformer(this.getById(this.selId));
        }
    },
    methods: {
        onmouseover(e) {
            const stage = e.target.getStage();
            stage.container().style.cursor = 'pointer';
        },
        onmouseout(e) {
            const stage = e.target.getStage();
            stage.container().style.cursor = 'default';
        },
        clearTransformer() {
            let stage = this.$refs.stage.getNode();
            let trs = stage.find('.rectTransformer');
            trs.forEach(x => {
                x.destroy();
            })
        },
        addTransformer(shape) {
            let stage = this.$refs.stage.getNode();
            const transformerNode = this.$refs.transformer.getNode().clone();
            transformerNode.name('rectTransformer');
            let layer = this.$refs.layer.getNode();
            layer.add(transformerNode);
            transformerNode.nodes([shape]);
        },
        shapeProperty(shape) {
            let kq = {
                x: shape.x(),
                y: shape.y(),
                width: shape.width(),
                height: shape.height(),
                scaleX: shape.scaleX(),
                scaleY: shape.scaleY(),
            }
            return kq;
        },
        shapeSetFull(shape, sceneWidth, sceneHeight) {
            shape.x(0);
            shape.y(0);
            shape.scaleX(1);
            shape.scaleY(1);
            shape.width(sceneWidth);
            shape.height(sceneHeight);
        },
        onFull() {
            let shape = this.getById(this.selId);
            this.shapeSetFull(shape, sceneWidth, sceneHeight);
            shape.moveToBottom();
            this.msg = this.shapeProperty(shape);
        },
        getShapeName(shape) {
            return shape.getAttr('shapename');
        },
        onClearSelect() {
            this.clearTransformer();
        },
        onSave() {
            let stage = this.$refs.stage.getNode();
            var dataURL = stage.toDataURL({ pixelRatio: 0.5 });
            //alert(dataURL);
            this.ds.push(dataURL);
        },
        handleDragstart(e) {
            e.target.moveToTop();
        },
        handleDragend(e) {
            e.target.fill('yellow');
        },
        onStageClick(e) {
            let p = this.myGetMouse();
            let layer = this.$refs.layer.getNode();

            if (e.target.id() == '') {
                //let circle=this.makeCircle(this.getCount(),p.x,p.y,70);
                //let circle=this.makeRect(this.getCount(),p.x,p.y,100,100);
                let circle = this.makeImg(this.getCount(), p.x, p.y, 700, 500, this.image);
                //let circle = this.makeCustom(this.getCount(), p.x, p.y);
                layer.add(circle);
            } else {
                this.selId = e.target.id();
            }
        },
        onStageMouseMove(e) {
            let p = this.myGetMouse();
        },
        onStageMouseenter(e) {
        },
        onStageMouseleave(e) {
        },
        getCount() {
            let kq = this.count;
            this.count++;
            return kq;
        },
        makeCircle(id, x, y, radius) {
            let circle = this.$refs.circle.getNode().clone();
            circle.id(id.toString());
            circle.x(x);
            circle.y(y);
            circle.radius(radius);
            circle.draggable(true);
            circle.fill(Konva.Util.getRandomColor());
            circle.stroke('blue');
            circle.setAttr("shapename", 'circle');
            return circle;
        },
        makeRect(id, x, y, w, h) {
            let circle = this.$refs.rect.getNode().clone();
            circle.id(id.toString());
            circle.x(x);
            circle.y(y);
            circle.width(w);
            circle.height(h);
            circle.draggable(true);
            circle.fill(Konva.Util.getRandomColor());
            circle.setAttr("shapename", 'rect');
            //circle.stroke('blue');
            return circle;
        },
        makeImg(id, x, y, w, h, image) {
            let circle = this.$refs.img.getNode().clone();
            circle.id(id.toString());
            circle.x(x);
            circle.y(y);
            circle.width(w);
            circle.height(h);
            circle.image(image);
            circle.draggable(true);
            circle.setAttr("shapename", 'img');
            return circle;
        },
        makeCustom(id, x, y) {
            let circle = this.$refs.custom.getNode().clone();
            circle.id(id.toString());
            circle.x(x);
            circle.y(y);
            return circle;
        },
        myGetMouse() {
            let scale = stage.scale().x;
            var mousePos = stage.getPointerPosition();
            var x = mousePos.x;
            var y = mousePos.y;
            var xx = Math.round(x / scale);
            var yy = Math.round(y / scale);
            return { x: xx, y: yy };
        },
        fitStageIntoParentContainer() {
            var container = document.querySelector('#stage-parent');
            var containerWidth = container.offsetWidth;
            var scale = containerWidth / sceneWidth;
            stage.width(sceneWidth * scale);
            stage.height(sceneHeight * scale);
            stage.scale({ x: scale, y: scale });
            this.configKonva.width = sceneWidth * scale;
            this.configKonva.height = sceneHeight * scale;
        },
        getById(id) {
            let layer = this.$refs.layer.getNode();
            return layer.find(`#${id}`)[0]
        },
    }
}
</script>
  